// services/commentsApi.js
import { createApi, fetchBaseQuery } from "@reduxjs/toolkit/query/react";

export const commentsApi = createApi({
  reducerPath: "commentsApi",
  baseQuery: fetchBaseQuery({
    baseUrl: "/api", // Ваш базовый URL для API
    credentials: "include", // Важно для работы с cookies и сессиями
  }),
  tagTypes: ["Comment"], // Для автоматического обновления списка комментариев
  endpoints: (builder) => ({
    // Получение списка комментариев
    getComments: builder.query<
      COMMENTS.GetCommentsResponse,
      COMMENTS.GetCommentsRequest
    >({
      query: () => "/comments/",
      providesTags: (result = [], error, arg) =>
        result.map(({ id }) => ({ type: "Comment", id })),
    }),

    // Создание нового комментария
    postComment: builder.mutation<
      COMMENTS.CreateCommentResponse,
      COMMENTS.CreateCommentRequest
    >({
      query: (newComment) => ({
        url: "/comments/",
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: newComment,
      }),
      invalidatesTags: [{ type: "Comment" }],
    }),

    // Обновление комментария
    updateComment: builder.mutation<
      COMMENTS.UpdateCommentResponse,
      COMMENTS.UpdateCommentRequest
    >({
      query: ({ id, updatedComment }) => ({
        url: `/comments/${id}/`,
        method: "PUT",
        headers: {
          "Content-Type": "application/json",
        },
        body: updatedComment,
      }),
      invalidatesTags: (result, error, { id }) => [{ type: "Comment", id }],
    }),

    // Удаление комментария
    deleteComment: builder.mutation<
      COMMENTS.DeleteCommentResponse,
      COMMENTS.DeleteCommentRequest
    >({
      query: ({ id }) => ({
        url: `/comments/${id}/`,
        method: "DELETE",
      }),
      invalidatesTags: (result, error, { id }) => [{ type: "Comment", id }],
    }),
  }),
});

export const {
  useGetCommentsQuery,
  usePostCommentMutation,
  useUpdateCommentMutation,
  useDeleteCommentMutation,
} = commentsApi;
